// standard

.btn {
  display: inline-block;
  border-radius: pxToVwMobile(30x);
  @include fluidDiff(border-radius, 30px, 30px);
  background: $color1;
  color: $white;
  @include fluidDiff(line-height, 40px, 60px);
  border: 2px solid $color1;
  text-transform: uppercase;
  @include fluidDiff(font-size, 13px, 15px);
  font-weight: $font-weight-bolder;
  @include fluidDiff(padding-left, 25px, 30px);
  @include fluidDiff(padding-right, 25px, 30px);
  transition:
          background ease-in-out .2s,
          border-color ease-in-out .2s,
          color ease-in-out .2s;

  &:hover {
    background: $black;
    color: $white;
    border-color: $black;
  }

  // flat

  &.btn--flat {
    @include fluidDiff(line-height, 36px, 36px);


  }

  // outline

  &.btn--outline {
    background: transparent;
    border-color: $black;
    color: $black;

    &:hover {
      border-color: $color1;
      color: $color1;
    }

  }

  // outline negative

  &.btn--outline-negative {
    background: transparent;
    border-color: $white;
    color: $white;

    &:hover {
      border-color: $color1;
      color: $color1;
    }

  }

}

// arrow-link

a {
  &.arrow-link {
    @include fluid(font-size, 16px);
    @include fluid(line-height, 25px);
    color: $colorText;
    i {
      @include fluid(font-size, 12px);
      @include fluid(margin-right, 15px);
      color: $colorBorder;
      transition: color ease-in-out .3s;
    }
    &:hover {
      i {
        color: $color1;
        opacity: 1;
      }
    }
  }
}

// arrows

.arrow-prev, .arrow-next {
  $arrowButtonSize: 45px;
  position: relative;
  display: inline-block;
  margin: 0 auto;
  @include fluid(width, $arrowButtonSize);
  @include fluid(height, $arrowButtonSize);
  &:before,
  &:after {
    content: '';
    position: absolute;
  }
  &:before {
    left: 50%;
    transform: translateX(-50%);
    @include fluid(width, $arrowButtonSize);
    @include fluid(height, $arrowButtonSize);
    @include fluid(border-radius, $arrowButtonSize);
    background: $color1;
  }
  &:after {
    $arrowSize: 10px;
    display: inline-block;
    top: 50%;
    left: 50%;
    @include fluid(width, $arrowSize);
    @include fluid(height, $arrowSize);
    @include fluid(border-top-width, 2px);
    @include fluid(border-right-width, 2px);
    border-top-style: solid;
    border-top-color: $white;
    border-right-style: solid;
    border-right-color: $white;
    transform: translateY(calc(-50%)) translateX(-50%) rotate(-135deg);
  }
}
.arrow-next {
  &:after {
    left: 46%;
    transform: translateY(calc(-50%)) translateX(-50%) rotate(45deg);
  }
}