$generalPadding: pxToVwMobile(20px);
.page-header--booking {
  background: $white;
  border-radius: pxToVwMobile(20px);
  padding: $generalPadding 0;

  .form-element {
    margin-bottom: 0;
  }

}
.booking-dates {
  padding: 0 $generalPadding $generalPadding $generalPadding;
  border-bottom: 1px solid rgba(0,0,0,.1);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: pxToVwMobile(14px);
  input {
    background-image: svg-load('../_images/svg/calendar.svg');
    background-repeat: no-repeat;
    background-position: right pxToVwMobile(8px) center;
    background-size: pxToVwMobile(22px) pxToVwMobile(19px);
    cursor: default;
    padding-left: pxToVwMobile(8px);
  }
}
.booking-numbers {
  padding: $generalPadding;
}
.booking-button {
  text-align: right;
  padding: 0 $generalPadding;
}

/* Numbers */

.form-element {
  &.booking-element--numbers {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: pxToVwMobile(14px);
    padding-bottom: pxToVwMobile(15px);
    label {
      margin-bottom: 0;
      line-height: pxToVwMobile(40px);
    }
    input[type=text] {
      text-align: center;
      padding: 0;
      border: none;
      font-size: pxToVwMobile(16px);
      font-weight: $font-weight-bolder;
    }
  }
}
.booking-numbers {
  .booking-element--numbers {
    &:last-child {
      padding-bottom: 0;
    }
  }
}
button {
  &.minus,
  &.plus {
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: $white;
    width: pxToVwMobile(40px);
    height: pxToVwMobile(40px);
    line-height: pxToVwMobile(40px);
    border-radius: pxToVwMobile(40px);
    text-indent: -9999em;
    position: relative;
    &:before,
    &:after {
      content: '';
      width: pxToVwMobile(14px);
      height: pxToVwMobile(2px);
      background: $black;
      position: absolute;
      top: 50%;
      left: 50%;
    }
    &:before {
      transform: translateX(-50%) translateY(-50%);
    }
    &:after {
      transform: translateX(-50%) translateY(-50%) rotate(90deg);
    }
  }
  &.minus {
    &:after {
      display: none;
    }
  }
}

/* Numberpicker */

.numberpicker {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-column-gap: pxToVwMobile(11px);
}