$formWidth: 500px;
$inputWidth: 380px;

input[type=email],
input[type=text],
input[type=date],
input[type=password],
select,
textarea {
  width: 100%;
  border: 1px solid $colorBorder;
  @include fluid(height, 40px);
  @include fluid(line-height, 40px);
  @include fluid(border-radius, 5px);
  @include fluid(font-size, 14px);
  @include fluid(padding-left, 15px);
  @include fluid(padding-right, 15px);
  padding-top: 0;
  padding-bottom: 0;

  &:focus {
    outline: none;
  }

}

.form-container {
  @include media-breakpoint-up(md) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 80px;
  }
}

.form-element {
  @include fluid(margin-bottom, 20px);
  label {
    text-transform: uppercase;
    font-weight: bold;
    @include fluid(font-size, 12px);
    @include fluid(line-height, 14px);
    @include fluid(margin-bottom, 7px);
    display: block;
  }
  textarea {
    @include fluid(padding, 15px);
    @include fluid(min-height, 200px);
  }
}

.form-columns--twocols {
  display: grid;
  grid-template-columns: repeat(2, auto);
  @include fluid(column-gap, 30px);
  @include media-breakpoint-up(md) {
    label {
      display: grid;
      grid-template-columns: auto pxToVw($inputWidth);
      @include media-breakpoint-up(xl) {
        grid-template-columns: auto $inputWidth;
      }
    }
  }
}
.form-columns--twocols-dates {
  grid-template-columns: repeat(2, 1fr);
  @include media-breakpoint-up(md) {
    grid-template-columns: repeat(3, 1fr);
  }

}
.form-columns--twocols-address {
  grid-template-columns: 33fr 66fr;
}
.form-element--small {

}
.form-element--medium {

}

@include media-breakpoint-up(md) {
  .inquiry-container {
    margin-bottom: 100px;
  }
  .inquiry-columns {
    column-count: 2;
    column-gap: 100px;
    break-inside: avoid;
  }
  .form--button-container {
    text-align: right;
    margin-bottom: 100px;
  }
}


