/* Breakpoints */

@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {
  $n: index($breakpoint-names, $name);
  @return if($n != null and $n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);
}
@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min != 0, $min, null);
}
@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {
  $next: breakpoint-next($name, $breakpoints);
  @return if($next, breakpoint-min($next, $breakpoints) - .02, null);
}
// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.
// Makes the @content-elements apply to the given breakpoint and wider.
@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the maximum breakpoint width. No query for the largest breakpoint.
// Makes the @content-elements apply to the given breakpoint and narrower.
@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {
  $max: breakpoint-max($name, $breakpoints);
  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.
// Makes the @content-elements apply between the min and max breakpoints
@mixin media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($lower, $breakpoints);
  $max: breakpoint-max($upper, $breakpoints);

  @if $min != null and $max != null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else if $max == null {
    @include media-breakpoint-up($lower, $breakpoints) {
      @content;
    }
  } @else if $min == null {
    @include media-breakpoint-down($upper, $breakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.
// No minimum for the smallest breakpoint, and no maximum for the largest one.
// Makes the @content-elements apply only to the given breakpoint, not viewports any wider or narrower.
@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  $max: breakpoint-max($name, $breakpoints);

  @if $min != null and $max != null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else if $max == null {
    @include media-breakpoint-up($name, $breakpoints) {
      @content;
    }
  } @else if $min == null {
    @include media-breakpoint-down($name, $breakpoints) {
      @content;
    }
  }
}

@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {
  @return if(breakpoint-min($name, $breakpoints) == null, "", "-#{$name}");
}

/* Units Stripping */

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

/* Fluid for mobile */

@function pxToVwMobile($value) {
  @return strip-unit($value) * 100 / strip-unit(375) + vw;
}

/* Fluid up to xl */

@function pxToVw($value) {
  @return strip-unit($value) * 100 / strip-unit($maxWidthDesktop) + vw;
}




@mixin fluidAll($property, $mobileValue, $value) {
  #{$property}: pxToVwMobile($mobileValue);
  @include media-breakpoint-up(md) {
    #{$property}: pxToVw($value);
  }
  @include media-breakpoint-up(xl) {
    #{$property}: $value;
  }
}





@mixin fluid($property, $value) {
  #{$property}: pxToVwMobile($value);
  @include media-breakpoint-up(md) {
    #{$property}: $value;
  }
}
@mixin fluidDiff($property, $mobileValue, $value) {
  #{$property}: pxToVwMobile($mobileValue);
  @include media-breakpoint-up(md) {
    #{$property}: $value;
  }
}










@mixin fluidDesktop($property, $value) {
  @include media-breakpoint-up(md) {
    #{$property}: pxToVw($value);
  }
  @include media-breakpoint-up(xl) {
    #{$property}: $value;
  }
}

/* Fluid up to xlplus */

@function pxToVwPlus($value) {
  @return strip-unit($value) * 100 / strip-unit($maxWidthDesktopPlus) + vw;
}
@mixin fluidPlus($property, $value) {
  #{$property}: pxToVwPlus($value);
  @include media-breakpoint-up(xlplus) {
    #{$property}: $value;
  }
}

/* Fluid up to outer */

@function pxToVwOuter($value) {
  @return strip-unit($value) * 100 / strip-unit($outerLimit) + vw;
}
@mixin fluidOuter($property, $value) {
  #{$property}: pxToVwOuter($value);
  @include media-breakpoint-up(outer) {
    #{$property}: $value;
  }
}

/* Icons */

@mixin generateIconSet($icon-list) {
  @for $i from 1 through length($icon-list) {
    $icon-name: nth($icon-list, $i);
    .icon.icon--#{$icon-name} {
      background-image: svg-load('#{$icon-path}#{$icon-name}.svg');
    }
  }
}

@mixin generateIconSetColorVariants($icon-list, $color, $color-class) {
  @for $i from 1 through length($icon-list) {
    $icon-name: nth($icon-list, $i);

    .icon.icon-color--#{$color-class}.icon--#{$icon-name} {
      background-image: svg-load('#{$icon-path}#{$icon-name}.svg', fill=$color);
    }
  }
}

@mixin setIcon($icon-name, $color, $size: 1em) {
  display: inline-block;
  vertical-align: middle;
  width: $size;
  height: $size;
  line-height: $size;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;

  background-image: svg-load('#{$icon-name}.svg', fill=$color);
}

@mixin updateIcon($icon-name, $color) {
  background-image: svg-load('#{$icon-name}.svg', fill=$color);
}

@mixin getIcon($icon-name, $color) {
  $icon-name: #{$icon-path}#{$icon-name};

  @include updateIcon($icon-name, $color);
}
