.page-footer {
  padding-top: pxToVwMobile(68px);
  padding-left: pxToVwMobile(35px);
  padding-bottom: pxToVwMobile(25px);
  padding-right: pxToVwMobile(35px);
  background: $colorBackgroundLight;
}
address {
  background: $white;
  text-align: center;
  font-size: pxToVwMobile(14px);
  line-height: pxToVwMobile(20px);
  border-radius: pxToVwMobile(20px);
  padding: pxToVwMobile(40px) pxToVwMobile(50px);
  max-width: pxToVwMobile(320px);
  margin: 0 auto pxToVwMobile(65px);
  & > div {
    margin-bottom: pxToVwMobile(20px);
  }
  .logo {
    background-image: svg-load('../_images/svg/logo.svg');
    width: pxToVwMobile(157.6px);
    padding-bottom: $logoRatio/2 + 0%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: pxToVwMobile(20px);
    float: none;
  }
  a {
    color: $black;
  }
}
.footer-menus {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: pxToVwMobile(50px);
}
.footer-menu {
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  li {
    margin-bottom: pxToVwMobile(15px);
  }
  a {
    &.arrow-link {
      font-size: pxToVwMobile(15px);
      line-height: pxToVwMobile(23px);
    }
  }
}
.newsletter {
  text-align: center;
  margin-bottom: pxToVwMobile(50px);
  input[type=email] {
    margin-bottom: pxToVwMobile(20px);
  }
  label {
    text-align: left;
    font-size: pxToVwMobile(14px);
    line-height: pxToVwMobile(20px);
    margin-bottom: pxToVwMobile(23px);
    display: grid;
    grid-template-columns: pxToVwMobile(30px) auto;
  }
}
.page-footer--bottom {
  border-top: 1px solid rgba($black, .3);
  padding-top: pxToVwMobile(15px);
  font-size: pxToVwMobile(14px);
  line-height: pxToVwMobile(20px);
  display: grid;
  grid-template-columns: 1fr auto;
  .logo--flat {
    margin-top: pxToVwMobile(5px);
  }
}
.copyright {
  opacity: .4;
  text-transform: uppercase;
}
