.page-header {
  position: relative;
  //height: $headerHeightMobile;
}
.portal {
  .page-header {
    //margin-bottom: pxToVwMobile(220px);
  }
}
.page-header--heroslider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.page-header--top,
.page-header--meta {
  position: relative;
}
.page-header--top {
  z-index: 2;
}
.page-header--meta {
  z-index: 1;
}
.page-header--logo {
  .logo {
    position: absolute;
    top: pxToVwMobile(65px);
    left: 50%;
    transform: translateX(-50%);
  }
}
.page-header--widgets {
  z-index: 1;
  //margin-top: calc(#{$headerHeightMobile} - #{pxToVwMobile(60px)});
  margin-top: pxToVwMobile(-40px);
  padding-left: pxToVwMobile(35px);
  padding-right: pxToVwMobile(35px);
}

@include media-breakpoint-up(md) {
  .page-header {
    height: $headerHeightDesktopContent;
  }
  .portal {
    .page-header {
      height: $headerHeightDesktopPortal;
      margin-bottom: 0;
    }
  }

  /* Header Layout */

  $widgetWidth: 370px;
  $widgetPaddingRight: 40px;
  $widgetColumnWidth: $widgetWidth + $widgetPaddingRight;

  .page-header {
    display: grid;
    grid-template-rows: pxToVw(110px) auto minmax(0, pxToVw(400px)) pxToVw(100px) pxToVw(50px);
    grid-template-columns: 1fr minmax(0, pxToVw(1180px)) minmax(0, pxToVw($widgetColumnWidth)) 1fr;
    grid-template-areas:
          '. top top .'
          '. . . .'
          '. . teaser .'
          '. . . .'
          'meta meta meta meta';
    @include media-breakpoint-up(xl) {
      grid-template-rows: 110px auto minmax(0, 400px) 100px 50px;
      grid-template-columns: 1fr minmax(0, 1180px) minmax(0, $widgetColumnWidth) 1fr;
    }
  }
  .page-header--top {
    grid-area: top;
    z-index: 1;
    display: grid;
    grid-template-columns: pxToVwPlus($logoWidth) auto;
    @include fluidPlus(padding-left, 30px);
    @include fluidPlus(padding-right, 30px);
    @include media-breakpoint-up(xlplus) {
      grid-template-columns: $logoWidth auto;
    }
  }
  .page-header--meta {
    grid-area: meta;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    @include fluidDesktop(padding-left, 30px);
    @include fluidDesktop(padding-right, 30px);
  }
  .page-header--meta-right {
    display: flex;
    justify-content: flex-end;
  }
  .page-header--logo {
    display: flex;
    padding: 0;
    .logo {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      align-self: flex-end;
      @include fluidPlus(margin-bottom, -7px);
    }
  }
  .page-header--nav {
    display: flex;
    justify-content: flex-end;
    .main-nav {
      align-self: flex-end;
    }
  }
  .page-header--widgets {
    grid-area: teaser;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
    padding-left: 0;
    @include fluidDesktop(padding-right, $widgetPaddingRight);
    margin: 0;
  }
}