body {
  //overflow-x: hidden;
  // exists only for scroll-reveal animations which cause horizontal scrollbars when animation is from left/right
  background: $color2;
}

.page-wrap {
  margin: 0 auto;
  max-width: 1920px;
  background: $white;
}

main[role=main] {
  @include fluid(padding-top, 100px);
  padding-left: 0;
  padding-right: 0;
}

/* Selection */

::selection {
  background: $color2;
  color: $white;
}

/* Container */

.container,
.container-narrow {
  @include fluid(padding-left, 30px);
  @include fluid(padding-right, 30px);
  margin: 0 auto;
}
.container {
  max-width: map-get($grid-breakpoints, xlplus);
}
.container-narrow {
  max-width: 1036px;
}

/* Basic containers */

main[role=main] {
  @include fluidDiff(padding-top, 60px, 100px);
  @include fluidDiff(padding-left, 20px, 0);
  @include fluidDiff(padding-right, 20px, 0);
  //padding-left: 0;
  //padding-right: 0;
}

/* Contentblocks */

.contentblock {

  @include fluidDiff(margin-bottom, 60px, 100px);

  &.margin-bottom {
    &-small {
      margin-bottom: pxToVwMobile(20px);
      @include fluidDiff(margin-bottom, 20px, 30px);
    }
    &-medium {
      @include fluidDiff(margin-bottom, 40px, 60px);
    }
  }


}