.contentblock--gallery {
  .container-narrow {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  //grid-gap: pxToVwMobile(20px);
  }
}
.gallery--image {
  display: block;
  figure {
    margin: 0;
  }
  img {
    width: 100%;
    height: auto;
  }
}

.contentblock--gallery {
  .container-narrow {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  }
}
.contentblock--gallery-large {
  .container-narrow {
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  }
}