.main-nav--toggle {
  //clip-path: circle(0);
  display: none;
}
@include media-breakpoint-down(lg) {

  .main-nav--burger {
    margin: 0;
    position: absolute;
    top: pxToVwMobile(17px);
    right: pxToVwMobile(15px);
    padding-top: pxToVwMobile(7px);
    padding-bottom: pxToVwMobile(7px);
    padding-top: 7px;
    padding-bottom: 7px;
    z-index: 2;
    span {
      display: block;
      width: pxToVwMobile(30px);
      height: pxToVwMobile(2px);
      width: 30px;
      height: 2px;
      background: $white;
    }
    &:before,
    &:after {
      content: '';
      display: block;
      width: pxToVwMobile(30px);
      height: pxToVwMobile(2px);
      width: 30px;
      height: 2px;
      background: $white;
      position: absolute;
      left: 0;
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
  }
  .main-nav {
    display: none;
    background: rgba($black, 0.8);
    width: 100%;
    height: 100vh;
    padding-right: pxToVwMobile(60px);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    overflow-y: scroll;
    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }
    .main-nav--first-level {

    }

    li {
      border-bottom: 1px solid rgba($color2, .5);
    }
    a,
    a.btn {
      display: block;
      color: $white;
      background: rgba($color1, .5);
      font-size: pxToVwMobile(14px);
      line-height: pxToVwMobile(50px);
      padding: 0 pxToVwMobile(20px);
      text-transform: uppercase;
      border: none;
      border-radius: 0;
      font-weight: $font-weight-base;
      &:hover {
        color: $white;
      }
    }

    .main-nav--sub {
      li {
        border-bottom: none;
      }
      a {
        background: rgba($color2, .5);
        padding: 0 pxToVwMobile(40px);
        text-transform: none;
      }
    }

  }
  .main-nav--toggle:checked {
    & + .main-nav {
      display: block;
    }
  }
}

@include media-breakpoint-up(md) {
  .main-nav--toggle {

  }
  .main-nav {
    ul {
      padding: 0;
      margin: 0;
      list-style-type: none;
    }

    /* special button */

    .btn--outline-negative {
      &:hover {
        background: $white;
        border-color: $white;
      }
    }

  }
  ul.main-nav--first-level {
    li {
      display: inline-block;
      @include fluidDesktop(padding-left, 15px);
      @include fluidDesktop(padding-right, 15px);
      position: relative;
      &:last-child {
        padding-right: 0;
      }
      &.main-nav--active {
        & > a {
          text-decoration: underline;
        }
      }
    }
    a {
      color: $white;
      text-transform: uppercase;
      font-weight: $font-weight-bolder;
      line-height: 40px;
      font-size: 15px;
    }
  }

  /* Dropdown */

  ul.main-nav--sub {
    position: absolute;
    /*top: 0;
    @include fluidDesktop(top, 30px);
    @include fluidDesktop(left, 15px);
    @include fluidDesktop(min-width, 320px);
    @include fluidDesktop(padding-top, 20px);
    @include fluidDesktop(padding-bottom, 20px);*/
    top: 30px;
    left: 15px;
    min-width: 320px;
    padding-top: 20px;
    padding-bottom: 20px;
    &:before {
      content: '';
      width: 0;
      height: 0;
      /*border-left-style: solid;
      border-left-color: transparent;
      border-right-style: solid;
      border-right-color: transparent;
      @include fluidDesktop(border-left-width, 7px);

      @include fluidDesktop(border-right-width, 7px);
      border-bottom-style: solid;
      border-bottom-color: $color1;*/
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 8px solid $color1;
      //@include fluidDesktop(border-bottom-width, 8px);
      position: absolute;
      left: 20px;
      top: 12px;
      /*@include fluidDesktop(left, 20px);
      @include fluidDesktop(top, 12px);*/
    }
    li {
      background: $color1;
      display: block;
      @include fluidDesktop(padding-left, 0);
      @include fluidDesktop(padding-right, 0);
      /*padding-left: 0;
      padding-right: 0;*/
      &:hover,
      &.main-nav--sub-active {
        a {
          background: rgba(0,0,0,.1);
        }
      }
    }
    a {
      display: block;
      /*@include fluidDesktop(line-height, 50px);
      @include fluidDesktop(padding-left, 30px);
      @include fluidDesktop(padding-right, 30px);*/
      line-height: 50px;
      padding-left: 30px;
      padding-right: 30px;
      transition: background .15s ease-in-out;
    }
  }

  /* Dropdown Animation */

  ul.main-nav--sub {
    transform: translateY(pxToVw(20px));
    opacity: 0;
    transition: all .33s ease-in-out;
    visibility: hidden;
  }
  ul.main-nav--first-level {
    li {
      &:hover {
        ul.main-nav--sub {
          visibility: visible;
          transform: translateY(0);
          opacity: 1;
        }
      }
    }
  }

  ul.main-nav--first-level {
    li {
      &:last-child,
      &:nth-last-child(2) {
        ul.main-nav--sub {
          left: auto;
          //@include fluidDesktop(right, 15px);
          right: 15px;
          &:before {
            left: auto;
            //@include fluidDesktop(right, 20px);
            right: 20px;
          }
        }
      }
    }
  }
}