

.page-header--meta {
  @include media-breakpoint-down(sm) {
    position: absolute;
    top: 0;
    left: 0;
  }
  padding: pxToVwMobile(15px) pxToVwMobile(20px);
  font-size: pxToVwMobile(14px);
  line-height: pxToVwMobile(20px);


  // Icons

  a {
    color: $white;
    i[class^=iconfont] {
      &:before {
        font-size: pxToVwMobile(20px);
        float: left;
      }
    }
  }

  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      display: inline-block;
    }
  }

}

// Lists

.page-header--meta-left {
  i {
    margin-right: pxToVwMobile(5px);
  }
}


@include media-breakpoint-up(md) {
  .page-header--meta {
    padding: 0;
    @include fluid(padding-left, 30px);
    @include fluid(padding-right, 30px);
    @include fluid(font-size, 14px);
    @include fluid(line-height, 50px);
    width: 100%;
    border-top: 1px solid rgba($white, .4);

    // Icons

    a {
      i[class^=iconfont] {
        &:before {
          @include fluid(font-size, 20px);
          @include fluid(line-height, 49px);
          float: left;
        }
      }
    }


  }

  // Lists

  .page-header--meta-left {
    li {
      @include fluid(margin-right, 20px);
    }
    i {
      @include fluid(margin-right, 10px);
    }
  }
  .page-header--meta-right {
    li {
      @include fluid(margin-left, 30px);
    }
  }
}