/* Teaserslider Controls */

.teaserslider {
  position: relative;
}
.teaserslider--controls {
  .arrow-prev,
  .arrow-next {
    position: absolute;
    @include fluid(top, 90px);
  }
  .arrow-prev {
    @include fluid(left, -22.5);
  }
  .arrow-next {
    @include fluid(right, -22.5px);
  }
}